.showPodcasts {
    background: rgb(0, 0, 0);
    border: 1px solid black;
    color: black;
    font-size: 14px;
    padding: 5px;
    position: absolute;
    right: 19%;
    top: 7%;
    width: 300px;
    z-index: 999999999999999999999999999;
}

.hidePodcasts {
    font-size: 48px;
    display: none;
}